import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_SET_COL_HEADER = Symbol('TABLE_SET_COL_HEADER');

export function setTableColHeader(node: Table) {
  return {
    type: TABLE_SET_COL_HEADER,
    payload: {
      node,
    },
  };
}

interface TableSetTableColHeaderPayload {
  node: Table;
}

interface TableSetTableColHeaderAction {
  type: typeof TABLE_SET_COL_HEADER;
  payload: TableSetTableColHeaderPayload;
}

export function isTableSetTableColHeaderAction(
  action: Action,
): action is TableSetTableColHeaderAction {
  return action.type === TABLE_SET_COL_HEADER;
}
